/*-------------------------------------------------------------------------------------------*/
/* BASIC STYLE    ||---------------------------- */
/*-------------------------------------------------------------------------------------------*/
body,
html {
	margin: 0;
	padding: 0;
}

body {
	background-color: #eef1f5;
	padding: 0 !important;
	margin: 0 !important;
	font-family: Poppins, sans-serif;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.7;
	color: #53505f;
}

.img-circle {
	border-radius: 50%;
}

.form-control {
	display: block;
	width: 100%;
	height: 39px;
	padding: 6px 12px;
	font-size: 14px;
	line-height: 1.42857143;
	color: #555;
	background-color: #fff;
	background-image: none;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
	transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
	outline: 0 !important;
	box-shadow: none !important;
}

.btn-text {
	background: 0 0;
	border: none;
	border-radius: 2px;
	color: #53505f;
	position: relative;
	height: 36px;
	margin: 0;
	min-width: 64px;
	padding: 0 16px;
	display: inline-block;
	font-size: 14px;
	font-weight: 500;
	text-transform: uppercase;
	letter-spacing: 0;
	overflow: hidden;
	outline: none;
	cursor: pointer;
	text-decoration: none;
	text-align: center;
	line-height: 36px;
	vertical-align: middle;
	outline: none;
}

.btn-text:focus {
	outline: none;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.18), 0 8px 16px rgba(0, 0, 0, 0.36);
	background-color: rgba(158, 158, 158, 0.4);
}

.btn:focus {
	outline: none !important;
	box-shadow: none;
}

/* ===================== */
.wrapper {
	height: 100%;
	position: relative;
	overflow-x: hidden;
	overflow-y: auto;
	/* padding-bottom: 60px; */
}

.wrapper:before,
.wrapper:after {
	content: " ";
	display: table;
}

/* Header */

.main-header {
	z-index: 1030;
	width: 100%;
	margin: 0;
	border: 0;
	padding: 0;
	-webkit-filter: none;
	        filter: none;
	background-color: #fff;
	box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.01), 0 15px 32px 0 rgba(0, 0, 0, 0.06);
	position: fixed;
	right: 0px;
	left: 0px;
	top: 0px;
	min-height: 70px;
}

.container_header {
	float: left;
	width: 100%;
	display: flex;
}

.min-h {
	min-height: 70px;
}

.logo {
	float: left;
	display: block;
	width: 270px;
	height: 70px;
	padding: 10px;
	line-height: 1.7 !important;
	background: #2a3142;
}

@media (max-width: 991px) {
	.logo {
		float: none;
		margin: 0 auto;
		text-align: center;
		height: 35px;
		width: 202px;
		padding: 0;
		position: static !important;
		left: 0;
		right: 0;
		margin: 0 auto;
		background: transparent;
	}
}

.logo .logo-default {
	display: inline-block;
}

.logo a .logo_icon {
	display: none;
}

.top_function {
	padding: 9px 0px;
}

.icon_menu {
	margin-right: 20px;
}

.search,
.icon_menu {
	display: inline-block;
}

.menu-toggler {
	line-height: 20px;
	cursor: pointer;
	/* opacity: .7; */
	filter: alpha(opacity=70);
	display: block;
	transition: opacity 0.3s;
	background: none;
	height: 20px;
	width: 25px;
	border-top: 2px solid #000;
	border-bottom: 2px solid #000;
	position: relative;
}

.menu-toggler:before {
	content: "";
	position: absolute;
	bottom: 7px;
	left: 0px;
	height: 2px;
	width: 100%;
	background: #2a3142;
}

#toggle_res_search {
	color: #53505f;
	font-size: 20px;
}

.search .search-form {
	background: #f3f4f4;
}

.search-form {
	width: 100%;
	max-width: 340px;
	max-height: 40px;
	background-color: transparent;
	color: #fff;
	border-radius: 30px;
	margin: 0px;
	border: 1px solid #e9ecef;
}

.search-form .form-control {
	background-color: transparent;
	border: 0;
	color: #1e2629;
	padding: 7px 12px;
}

.search .input-group .btn {
	padding: 10px;
	line-height: 1;
	background: #f3f4f4;
	color: #2a3142;
	height: 39px;
	border-radius: 0 20px 20px 0;
	width: 60px;
}

.search .input-group .btn i {
	font-size: 20px;
}

@media (min-width: 992px) {
	#search_form {
		width: 243px;
	}
}

@media (min-width: 992px) {
	#search_form {
		display: block;
		height: auto !important;
		overflow: visible;
	}

	#toggle_res_search {
		display: none;
	}
}

@media (max-width: 991px) {
	#search_form {
		left: 0;
		margin: 0 !important;
		position: absolute;
		width: 100%;
		padding-top: 7px;
		top: 0;
		z-index: 99999;
		max-width: none;
		background: #fff;
		height: 70px;
		max-height: none;
	}

	.search_col {
		position: static;
	}

	.search-form .form-control {
		border-radius: 0;
		height: 60px;
	}

	.search .input-group .btn {
		background: none;
		color: #53505f;
		height: 60px;
	}

	.search .input-group .btn i {
		font-size: 24px;
	}

	.search-form {
		border-radius: 0;
	}
}

.fullscreen-btn {
	padding: 0;
	line-height: 20px;
	position: relative;
	display: inline-block;
}

@media (max-width: 767px) {
	.fullscreen-btn {
		display: none;
	}
}

.fullscreen-btn a {
	color: #53505f;
}

.language-switch {
	margin: 0;
	display: inline-block;
	min-width: 109px;
	text-align: right;
	margin-left: 20px !important;
}

.language-switch,
.dropdown-notification,
.dropdown-inbox,
.dropdown-user {
	margin: 0 0px 0px 20px;
	padding: 10px 0px;
}

@media (max-width: 991px) {
	.language-switch,
	.dropdown-notification,
	.dropdown-inbox,
	.dropdown-user {
		margin: 0 0px 0px 24px;
	}
}

.language-switch .btn {
	background: transparent;
	font-size: 14px;
	padding: 0px;
	color: #53505f;
}

.language-switch .btn span {
	vertical-align: middle;
}

.btn-default {
	text-transform: capitalize;
}

.btn-default a {
