/* form-wizard */
.form-wizard {
	position: relative;
	display: table;
	max-width: 540px;
	margin: 0 auto 30px;
}

.form-wizard .steps {
	overflow: hidden;
	margin: 40px 0;
}

.form-wizard .steps ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.form-wizard .steps ul li {
	position: relative;
	float: left;
	padding: 0 10px;
	cursor: pointer;
	transition: all 0.4s ease-in-out;
}

.form-wizard .steps ul li.active,
.form-wizard .steps ul li:hover {
	color: #292b2c;
}

.form-wizard .steps ul li.active::after,
.form-wizard .steps ul li:hover::after {
	width: 100%;
	background: #a768f3;
}

.form-wizard .steps ul li::before {
	position: absolute;
	top: 20px;
	left: -50%;
	width: 100%;
	height: 2px;
	content: "";
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}

.form-wizard .steps ul li span,
.form-wizard .steps ul li::after,
.form-wizard .steps ul li::before {
	transition: all 0.4s ease-in-out;
	background: #e5e9ec;
}

.form-wizard .steps ul li::after {
	top: 20px;
	left: -50%;
	content: "";
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	position: absolute;
	height: 2px;
	width: 0;
}

.form-wizard .steps ul li span {
	position: relative;
	z-index: 1;
	display: block;
	width: 35px;
	height: 35px;
	margin: 0 auto 15px;
