/* INDEX1 */
.bg_color {
	position: fixed;
	top: 0;
	left: 0;
	padding-top: 70px;
	height: 100vh;
	width: 270px;
	background: #2a3142;
	padding-bottom: 20px;
	z-index: 9;
}
.bg_color:before {
	display: none;
}

.bg_color .sidebar-menu li a {
	margin: 0;
	padding: 20px 15px;
	text-decoration: none;
	font-size: 14px;
	display: block;
	border-bottom: 1px solid #272c3a;
	color: #999;
	line-height: 1.2;
	transition: all 0.3s;
}
.bg_color .sidebar-menu li a:hover {
	background: #272c3a;
	color: #fff;
}

/*==========Header============*/
.icon_menu {
	display: block;
	position: absolute;
	top: 25px;
	right: 0;
	margin: 0px;
	z-index: 99999;
}

