/**
    base
*/
// @import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: sans-serif, serif;
    font-size: 14px;
}

.tooltip {
    padding: 5px;
    background: rgb(153, 44, 44);
    position: absolute;
    z-index: 22;
    color: #fff;
    opacity: 0;
    border-radius: 4px;
    transition: .3s all;

    &-time {
        padding: 2px 5px;
        background: red;
        border-radius: 5px;
        transition: 0;
    }

    &-percent {
        transition: 0;
    }
}

.tooltip-shown {
    opacity: 1;
}

.circle {
    cursor: pointer;
}

body {
    background: #e7e7e7;
    font-family: 'Lato';
    font-weight: 400;
    overflow-x: hidden;
}

.content {
    &-wrap {
        position: relative;
        background: #e7e7e7;
    }
}

a {
    text-decoration: none;
    color: #333;
}

.red {
    background: red !important;
    border: 1px solid red !important;
    color: #fff !important;
}

.green {
    background: green !important;
    border: 1px solid green !important;
    color: #fff !important;
}

.data-picker {
    background: #fff;
    height: 100%;
    flex-grow: 1;
}

.multi-input-item {
    height: 30px;
    border: 1px solid #e7e7e7;
}

.multi-input-item:first-child {
    border-radius: 10px 0 0 10px;
}

.multi-input-item:last-child {
    border-radius: 0 10px 10px 0;
}

.MuiBadge-colorError {
    // border-radius: 4px !important;
    height: 10px !important;
    width: 10px !important;
    min-width: 10px !important;
}

.modal {
    z-index: 9999 !important;
}

@media print {

    #ourPdf {
        font-size: 6px;
    }

    .filter-aside {
        display: none;
    }

    header {
        display: none !important;
    }

}


.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}

.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.modal-body,
.modal-footer,
.modal-header {
    padding: 10px 5px !important;
}

.modal-header {
    padding: 10px !important;
}

.user-panel > .user_image > img {
    min-height: 80px;
}


