.filters {
    width: 200px;
    padding: 10px;
    height: 100%;
}

.filter {
    list-style-type: none;
    min-height: 50px;
    border-radius: 5px;
    min-height: 60px;
    margin-left: -5px;

    &-wrapper {
        display: flex;
    }

    &-search {
        width: 100%;
        height: 30px;
        padding: 7px;
        padding-left: 10px;
        border: none;
        box-shadow: 0 0 5px 1px #333;
        border-radius: 4px;
        margin-bottom: 5px;
    }

    &-title {
        color: #333;
        margin-bottom: 10px;
        font-size: 14px;
        margin-top: 12px;
    }

    &-aside {
        height: 100%;
        width: 210px;
    }

    &-container {
        height: calc(100vh - 100px);
        background: #e7e7e7;
    }

    &-item {
        position: relative;
        overflow: hidden;
        font-size: 14px !important;
        text-transform: none !important;
        text-align: left !important;
        border-radius: 5px;
        padding-left: 15px;
        padding-right: 10px;
        margin-bottom: 6px;
        color: #fff;
        background: #3d9970;
        height: 27px;
        display: flex;
        align-items: center;
        font-weight: 500;
        line-height: 20px;

        &:hover {
            background: #047d48;
        }

        &-modal {
            &-active {
                background-color: rgb(76, 76, 218);
                padding: 0 4px;
                border-radius: 3px;
                color: #fff;
            }
        }

        &-f {
            // background: #405065;

            &:hover {
                background: #314f6d;
            }
        }

        & span {
            position: absolute;
            top: 3px;
            left: 3px;
        }
    }

    &-icon {
        position: absolute;
        right: 13px;
        display: none;
    }

    &-wrap {
        margin-bottom: 15px;
        margin-top: 10px;
    }

    &-content {
        background: #e1e1e1;
        width: 100%;
    }

    &-selected-items {
        width: 100%;
        min-height: 18px;
        background: #db2b34;
        display: flex;
        align-items: center;
        padding: 4px 6px;
        display: none;
    }

    &-select-item {
        background: #333;
        color: #fff;
        padding: 3px 5px;
        font-size: 10px;
        text-transform: lowercase;
        border-radius: 6px;
        margin-right: 10px;
    }
}

#select .filter-title {
    margin-top: 18px;
}

.filter,
.modal-filter {
    min-height: 105px;
    user-select: none;
    /* supported by Chrome and Opera */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
}

.modal-filter {
    height: 200px;
    min-height: 200px;
}

#setting-table {
    & .filter-icon {
        display: block;
    }

    & .filter {
        min-height: 150px;
    }
}

#select .filter-item {
    background-color: #6c757d;
}

#setting-table .filter-item {
    background-color: rgb(169, 30, 61) !important;
}

.reset-all {
    position: relative;
    font-size: 16px !important;
    text-transform: none !important;
    border-radius: 5px;
    padding-left: 15px;
    padding-right: 10px;
    margin-bottom: 6px;
    color: #fff;
    background: rgb(204, 9, 9);
    height: 27px;
    font-weight: 600;
    border: rgb(204, 9, 9);
    margin-bottom: 10px;
    cursor: pointer;
}

.material {
    position: fixed;
    bottom: 40px;
    right: 40px;
    z-index: 900;
    cursor: pointer;
    display: flex;
    align-items: flex-end;
    height: 60px;

    &-icon {
        background: #26a69a;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .6s all;

        &:hover {
            transform: rotate(360deg);
        }
    }

    &-list {
        width: 60px;
        height: 60px;
        position: absolute;
        bottom: 0;
        left: 0;
    }
}

.pdf {
    width: 40px;
    height: 40px;
    background: #880e4f;
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 5px;
    transition: .4s top;
    z-index: -1;
    transition: .3s all;

    &:hover {
        transform: scale(1.1);
    }
}

.cvs {
    width: 40px;
    height: 40px;
    background: #e91e63;
    color: #fff;
    font-size: 13px;
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 5px;
    transition: .4s top;
    z-index: -1;
    transition: .3s all;

    &:hover {
        transform: scale(1.1);
    }
}

.material:hover {
    height: 160px;
}

.material:hover .cvs {
    top: -50px;
}

.material:hover .pdf {
    top: -100px;
}

.material:hover .material-icon {
    transform: rotate(360deg);
}

.modal {
    z-index: 2;

    &-filter {
        list-style-type: none;
        overflow-y: auto;
        min-height: 70px;
        box-shadow: 0 0 2px #333;
        border-radius: 3px;
        padding: 5px;
        flex-grow: 1;
        max-height: 200px;
        margin-top: 15px;

        &-wrap {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            width: 100vw;
            min-height: 100vh;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            background: rgba(84, 84, 84, 0.5);
            z-index: 999;
        }

        &-back {
            position: fixed;
            display: none;
            width: 100vw;
            height: 100vh;
            background: rgba(0, 0, 0, .7);
            z-index: 990;
        }
    }

    &-header {
        padding: 10px;
        font-weight: bold;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &-body {
        padding: 10px;
        background: #fff;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-grow: 1;

        &-select {
            width: 49%;
            padding: 10px 5px;
            border-radius: 2px;
            display: flex;
            flex-direction: column;
        }

        &-selected {
            width: 49%;
            padding: 10px 5px;
            border-radius: 2px;
            display: flex;
            flex-direction: column;
        }

        & .filter-item {
            background: #26a69a;
        }
    }

    &-footer {
        padding: 10px 15px;
        background: rgb(244, 244, 244);
        display: flex;
        justify-content: space-between;
    }

    &-back {
        position: fixed;
        width: 100vw;
        height: 100vh;
        background: rgba(84, 84, 84, 0.5);
    }
}


.selector {
    &-wrap {
        min-width: 120px;
        height: 30px;
        margin-left: 7px;
        border-radius: 4px;
        position: relative;
        border: 1px solid #b9b0b0;
        cursor: pointer;
    }

    &-list {
        width: 120px;
        position: absolute;
        border-radius: 4px;
        overflow: hidden;
        top: 40px;
        left: 0;
        background: #fff;
        box-shadow: 0 0 10px #333;
        display: none;
        z-index: 2;
    }

    &-item {
        padding: 5px;
        height: 30px;
        cursor: pointer;

        &:hover {
            background: #333;
            color: #e1e1e1;
        }
    }

    &-display {
        padding: 5px;
    }

}

#branches,
#date,
#user {
    display: none;
}

.free-points-list {
    padding: 12px 20px 8px;
    background: #6c7ae0;
}

.free-points-item {
    color: #333;
    background: #fff;
    margin-bottom: 11px;
    padding: 5px;
    border-radius: 3px;
    box-shadow: 0 0 2px #333;
    display: inline-block;
    width: 240px;

    & p {
        font-size: 16px;
        font-weight: 400;
    }
}

.cagi {
    display: flex;

    & span {
        color: red;
        border: 1px solid blue;
        padding: 3px;
    }
}

.filtder {
    padding: 4px;
    border-radius: 5px;
    user-select: none;
    /* supported by Chrome and Opera */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
}

.hundler-wrap {
    height: 100%;
    width: 23px;
    position: absolute;
    background: #154477;
    left: 0;
    border-radius: 4px 0 0 4px;
}

.filter-btn {
    padding: 6px;
    border-radius: 20px;
    border: none;
    font-size: 16px;
    font-weight: 700;
    cursor: pointer;
    outline: none;
    background: #6c7ae0;
    color: white;
    transition: .3s;
    margin-bottom: 5px;

    &:hover {
        background: #4954a3;
    }
}

.branches-icon {
    cursor: pointer;
    transition: .3s;

    &:hover {
        transform: scale(1.1);
    }
}

a>svg {
    margin-right: 10px;
}