.right_bar_top .dropdown-menu {
	left: auto !important;
	right: 0 !important;
	min-width: 175px;
	z-index: 1000;
	list-style: none;
	text-shadow: none;
	padding: 0;
	background-color: #fff;
	-webkit-border-radius: 2px;
	border: none;
	box-shadow: 0 3px 14px rgba(0, 0, 0, 0.1) !important;
	margin: 0px;
	transform: translate3d(0px, 50px, 0px) !important;
	top: 100% !important;
	transition: all 0.5s;
	display: block;
	visibility: hidden;
	opacity: 0;
	/* padding: 10px 0; */
}

.right_bar_top .dropdown-menu.show {
	transform: translate3d(0px, 0px, 0px) !important;
	visibility: visible;
	opacity: 1;
}

.right_bar_top .dropdown-menu:before {
	position: absolute;
	top: -7px;
	right: 9px;
	display: inline-block !important;
	border-right: 7px solid transparent;
	border-bottom: 7px solid #eff0f4;
	border-left: 7px solid transparent;
	/* border-bottom-color: rgba(0, 0, 0, .2); */
	content: "";
}

.dropdown-menu li a {
	color: #555;
	font-size: 13px;
	padding: 8px 16px;
	color: #6f6f6f;
	text-decoration: none;
	display: block;
	clear: both;
	font-weight: 300;
	line-height: 18px;
	white-space: nowrap;
}

.dropdown-notification .dropdown-toggle i,
.dropdown-inbox .dropdown-toggle i {
	color: #fff;
	font-size: 14px;
	display: inline-block;
	-webkit-font-smoothing: antialiased;
	text-decoration: none;
}

.dropdown-notification .dropdown-toggle,
.dropdown-inbox .dropdown-toggle,
.dropdown-user .dropdown-toggle {
	margin: 0;
	padding: 0;
	display: block;
	position: relative;
	text-decoration: none;
	background: #2a3142;
	border-radius: 50%;
	height: 35px;
	width: 35px;
	line-height: 35px;
	text-align: center;
}

.dropdown-notification .dropdown-toggle:after,
.dropdown-inbox .dropdown-toggle:after,
.dropdown-user .dropdown-toggle:after {
	display: none;
}

.dropdown-notification .dropdown-menu,
.dropdown-inbox .dropdown-menu {
	min-width: 160px;
	max-width: 355px;
	width: 355px;
	z-index: 9995;
	height: 300px;
}

