/** @format */
* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

body {
	overflow-x: hidden;
	font-family: sans-serif, serif;
}

ul {
	list-style-type: none;
}
#alert-cant-save {
	position: fixed;
	top: 20px;
	right: -240px;
	-webkit-transition: 0.6s;
	-o-transition: 0.6s;
	transition: 0.6s;
}
.alert {
	padding: 0.75rem 1.25rem;
	margin-bottom: 1rem;
	border: 1px solid transparent;
	border-radius: 0.25rem;
}
.alert-danger {
	color: #721c24;
	background-color: #f8d7da;
	border-color: #f5c6cb;
}
.content {
	-webkit-box-flex: 1;
	-ms-flex-positive: 1;
	flex-grow: 1;
	padding: 0 20px;
	height: calc(100% - 60px);
}

.wrap {
	height: calc(100vh - 80px);
	max-height: calc(100vh - 142px);
	background: #f9f9f9;
	padding-top: 15px;
}

.pagination {
	padding: 15px;
	margin-left: -15px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.pagination-item {
	width: 25px;
	height: 25px;
	border-radius: 5px;
	border: 1px solid #333;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	cursor: pointer;
	margin-right: 7px;
}



.search-preloader {
	display: none;
}

#search-td {
	height: 30px;
	width: 150px;
	-webkit-appearance: none;
	border: none;
	border-radius: 4px;
	padding: 0 7px;
	font-size: 13px;
}

#search-indicator {
	display: none;
}

.table-search {
	display: none;
}

#search-list {
	position: absolute;
	left: 0;
	top: 32px;
	width: 100%;
	background: white;
	max-height: 200px;
	overflow-y: auto;
	z-index: 22;
	box-shadow: 0 0 10px #e7e7e7;
	transform: scaleY(0);
	transform-origin: top;
	transition: 0.3s;
}
.search-list-item {
	padding: 4px 6px;
}

/**
spinner
*/
#preloader-table {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	width: 100%;
	height: 100%;
}

.lds-spinner {
	color: official;
	display: inline-block;
	position: relative;
	width: 80px;
	height: 80px;
}

.lds-spinner div {
	-webkit-transform-origin: 40px 40px;
	-ms-transform-origin: 40px 40px;
	transform-origin: 40px 40px;
	-webkit-animation: lds-spinner 1.2s linear infinite;
	animation: lds-spinner 1.2s linear infinite;
}

.lds-spinner div:after {
	content: " ";
	display: block;
	position: absolute;
	top: 3px;
	left: 37px;
	width: 6px;
	height: 18px;
	border-radius: 20%;
	background: #1d0e0e;
}

.lds-spinner div:nth-child(1) {
	-webkit-transform: rotate(0deg);
	-ms-transform: rotate(0deg);
	transform: rotate(0deg);
	-webkit-animation-delay: -1.1s;
	animation-delay: -1.1s;
}

.lds-spinner div:nth-child(2) {
	-webkit-transform: rotate(30deg);
	-ms-transform: rotate(30deg);
	transform: rotate(30deg);
	-webkit-animation-delay: -1s;
	animation-delay: -1s;
}

.lds-spinner div:nth-child(3) {
	-webkit-transform: rotate(60deg);
	-ms-transform: rotate(60deg);
	transform: rotate(60deg);
	-webkit-animation-delay: -0.9s;
	animation-delay: -0.9s;
}

.lds-spinner div:nth-child(4) {
	-webkit-transform: rotate(90deg);
	-ms-transform: rotate(90deg);
	transform: rotate(90deg);
	-webkit-animation-delay: -0.8s;
	animation-delay: -0.8s;
}

.lds-spinner div:nth-child(5) {
	-webkit-transform: rotate(120deg);
	-ms-transform: rotate(120deg);
	transform: rotate(120deg);
	-webkit-animation-delay: -0.7s;
	animation-delay: -0.7s;
}

.lds-spinner div:nth-child(6) {
	-webkit-transform: rotate(150deg);
	-ms-transform: rotate(150deg);
	transform: rotate(150deg);
	-webkit-animation-delay: -0.6s;
	animation-delay: -0.6s;
}

.lds-spinner div:nth-child(7) {
	-webkit-transform: rotate(180deg);
	-ms-transform: rotate(180deg);
	transform: rotate(180deg);
	-webkit-animation-delay: -0.5s;
	animation-delay: -0.5s;
}

.lds-spinner div:nth-child(8) {
	-webkit-transform: rotate(210deg);
	-ms-transform: rotate(210deg);
	transform: rotate(210deg);
	-webkit-animation-delay: -0.4s;
	animation-delay: -0.4s;
}

.lds-spinner div:nth-child(9) {
	-webkit-transform: rotate(240deg);
	-ms-transform: rotate(240deg);
	transform: rotate(240deg);
	-webkit-animation-delay: -0.3s;
	animation-delay: -0.3s; 
}

.lds-spinner div:nth-child(10) {
	-webkit-transform: rotate(270deg);
	-ms-transform: rotate(270deg);
	transform: rotate(270deg);
	-webkit-animation-delay: -0.2s;
	animation-delay: -0.2s;
}

.lds-spinner div:nth-child(11) {
	-webkit-transform: rotate(300deg);
	-ms-transform: rotate(300deg);
	transform: rotate(300deg);
	-webkit-animation-delay: -0.1s;
	animation-delay: -0.1s;
}

.lds-spinner div:nth-child(12) {
	-webkit-transform: rotate(330deg);
	-ms-transform: rotate(330deg);
	transform: rotate(330deg);
	-webkit-animation-delay: 0s;
	animation-delay: 0s;
}

@-webkit-keyframes lds-spinner {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@keyframes lds-spinner {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

/** @format */
.header {
	padding: 24px 0 0;
	color: #c4c4c4;
	font-size: 12px;
}

.header-content {
	width: 100%;
	padding-left: 50px;
	padding-right: 50px;
	padding-bottom: 12px;
}

.btn-back {
	position: absolute;
	left: -100%;
	top: 0;
	bottom: 0;
	margin: auto;
	height: 24px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	cursor: pointer;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
}

.btn-back span {
	font-size: 17px;
	font-weight: bold;
	color: #c13544;
	text-transform: uppercase;
	margin-left: 5px;
}

.btn-back-shown {
	left: 10px;
}

.btn-gotable {
	position: absolute;
	left: 20px;
	border: 1px solid #434343;
	border-radius: 5px;
	padding: 5px 10px;
	color: #fff;
	background: #434343;
	cursor: pointer;
	font-weight: bold;
	text-transform: uppercase;
	-webkit-transition: 0.2s;
	-o-transition: 0.2s;
	transition: 0.2s;
}

.btn-gotable:hover {
	border: 1px solid #e73e47;
	background: #e73e47;
}



.search-wrap {
	position: absolute;
	height: 33px;
	right: 50px;
}

.branchgo-btn {
	position: absolute;
	left: 50px;
	color: #333;
	background: #fff;
	padding: 11px 9px 9px;
	border-radius: 5px;
	text-decoration: none;
	font-weight: 700;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
	text-transform: uppercase;
	min-width: 80px;
	text-align: center;
}

.tabs {
	width: 100%;
	position: relative;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	border-top: 1px solid #e7e7e7;
	padding-left: 50px;
	padding-right: 50px;
	height: 58px;
}

.tab {
	padding: 12px 20px;
}

.tab-btn {
	font-size: 14px;
	padding: 5px;
	cursor: pointer;
	border-radius: 5px;
	position: relative;
	color: #fff;
}

.tab-btn:hover {
	background: rgba(218, 58, 66, 0.4);
}

.tab-btn:before {
	position: absolute;
	content: "";
	height: 3px;
	width: calc(100% - 10px);
	background: #fff;
	bottom: -1px;
	left: 5px;
	display: none;
}

.tab-btn__active:before {
	display: block;
}

.home {
	width: 33px;
	height: 33px;
	border-radius: 5px;
	background: #818181;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	margin-left: 10px;
}

/** @format */
a {
	text-decoration: none;
	color: #333;
}
table {
	border-collapse: collapse;
	width: 100%;
	position: relative;
}
