#ourPdf {
    padding-right: 10px;
}

table {
    color: #333;
    background: white;
    margin: 10px 10px 0 0;
}

td,
th {
    padding: 12px 10px;
    background: white;
    border-width: 1px !important;
    border-style: solid !important;
    border-color: rgb(229, 233, 236) !important;
    border-image: initial !important;
    text-transform: lowercase !important;
    text-transform: capitalize !important;
    text-align: left !important;
}

tr {
    background: white;
}

tr:not(:first-child) td,
tr:not(:first-child) th {
    text-align: left;
}

.table-striped {
    & .btn-warning {
        background: #ffb642;
        border: 1px solid #ffb642;
        border-radius: 0;
        color: #fff;
        font-weight: 300;
        text-transform: uppercase;
    }

}

.btn-success {
    border-radius: 0;
    font-weight: 300;
    text-transform: uppercase;
}

.btn-light {
    border-radius: 0;
    border: 1px solid rgb(175, 173, 173) !important;
    font-weight: 300;
    text-transform: uppercase;
}